import uniListItem from "@/components/uni-ui/uni-list-item/uni-list-item.vue"
export default {
	components:{
		uniListItem
	},
	data() {
		return {
			id:0,
			express:{
				id:0,
				name:"",
				phone:""
			}
		}
	},
	onLoad(e) {
		if(!e.detail){
			return uni.showToast({
				title: '非法参数',
				icon: 'none'
			});
		}
		this.id = (JSON.parse(e.detail)).id
		this.getShipInfo()
	},
	methods: {
		getShipInfo(){
			this.$H.get('/order/order/shipInfo/'+this.id,{},{
				token:true
			}).then(res=>{
				
				this.express = res
				console.log(res)
			})
		}
	}
}
